import { Anchor, Button, Switch, Form, Image, Message } from '@arco-design/web-react'
import { useState, useEffect } from 'react'
import '../../css/setting.css'
// import { alertText } from '../util/dialog/index'
// let { alertTextLive2d } = window.electronAPI
let AnchorLink = Anchor.Link
let downLoadTimer
// let models = [
//     {
//         name:'shizuku',
//         label:''
//     },
//     {
//         name:'xxban',
//         label:''
//     },{
//         name:'22-0default',
//         label:'22'
//     },{
//         name:'33-0default',
//         label:'33'
//     },{
//         name:'haruto',
//         label:'()'
//     },{
//         name:'koharu',
//         label:'()'
//     },{
//         name:'hijiki',
//         label:''
//     },{
//         name:'tororo',
//         label:''
//     },{
//         name:'wanko',
//         label:''
//     },
// ]
function Setting(props){
    let {setBg, setSubataShow} = props
    let [btnSetting, setbtnSetting] = useState(localStorage.getItem('btnSetting')|| true)
    let [btnSetting1, setbtnSetting1] = useState(localStorage.getItem('btnSetting1') || true)
    let [btnSetting2, setbtnSetting2] = useState(localStorage.getItem('btnSetting2') || true)
    let [imgNum, setimgNum] = useState(localStorage.getItem('imgNum')? localStorage.getItem('imgNum')*1:0)
    // eslint-disable-next-line no-unused-vars
    let [imgs, setImgs] = useState([])
    let [path, setPath] = useState(window.wizPath)
    // let [liveName, setLive2d] = useState(localStorage.getItem('live2d') || 'defalut')
    let [zhSound, setZhSound] = useState(false)
    let [loadFile, setLoadFile] = useState(false)
    let [fileLength, setLength] = useState(0)
    let [currentFile, setCurrentFile] = useState(0)
    let [total, setTotal] = useState(0)
    let [current, setCurrent] = useState(0)
    // let [live2dOpen, setlive2dOpen] = useState(false)
    // eslint-disable-next-line react-hooks/exhaustive-deps
    useEffect(() => {
        // alert('window.wizPath')
        setPath(window.wizPath)
    })
    useEffect(()=>{
        // window.L2Dwidget.init({ 
        //     "model": {jsonPath:`./Resources/live2dModel/${liveName}/model.json`,"scale": 1 }, 
        //     // "dialog":{enable: true}
        //     display: {
        //         position: 'left',//
        //     },
        // });
        if(localStorage.getItem('btnSetting2') === null){
            localStorage.setItem('btnSetting2', true)
        }
        if(localStorage.getItem('btnSetting1') === null){
            localStorage.setItem('btnSetting1', true)
        }
        if(localStorage.getItem('btnSetting') === null){
            localStorage.setItem('btnSetting', true)
        }
    },[])
    useEffect(()=>{
        // alertTextLive2d(`${imgNum+1}`)

    }, [imgNum])
    return <div className="setting">
        <div className='setting-left'>
            <Anchor affix={false} hash={false} scrollContainer={'#setting-right'}>
                <AnchorLink href='#bg' title='' />
                <AnchorLink href='#setting' title='' />
                <AnchorLink href='#gameFile' title='' />
                {/* <AnchorLink href='#live2d-set' title='Live2d' /> */}
                <AnchorLink href='#language' title="!"/>
                <AnchorLink href='#clear' title='' />
                {/* <AnchorLink href='#bug' title='bug' /> */}
            </Anchor>
        </div>
        <div className='setting-right' id='setting-right'>
            <div className='setting-item' id='bg'>
                {/* <PageHeader title=''/> */}
                <div className='setting-bg'>
                    <Image onClick={()=>{
                        setimgNum(0)
                        setBg(0)
                        localStorage.setItem('imgNum', 0)
                    }} className={imgNum === 0?'arco-image-active':''} preview={false} style={{objectFit:'cover'}} width={100} height={100} src='https://vkceyugu.cdn.bspapp.com/VKCEYUGU-a3e579e1-12c0-4985-8d49-3ab58c03387a/c3e29bb5-d5a0-4799-a544-264e310356aa.jpg'/>
                    <Image onClick={()=>{
                        setimgNum(1)
                        setBg(1)
                        localStorage.setItem('imgNum', 1)
                    }}  className={imgNum === 1?'arco-image-active':''} preview={false} style={{objectFit:'cover'}} width={100} height={100} src='https://vkceyugu.cdn.bspapp.com/VKCEYUGU-a3e579e1-12c0-4985-8d49-3ab58c03387a/151d0d95-b330-4ca8-9de7-d0336aed9872.webp'/>
                    {
                        imgs.map((img, index)=>{
                            return <Image key={index} onClick={()=>{
                                setimgNum(index + 2)
                                setBg(index + 2)
                                localStorage.setItem('imgNum', index + 2)
                            }} className={(index + 2 === index)?'arco-image-active':''} preview={false} style={{objectFit:'cover'}} width={100} height={100} src={img}/>
                        })
                    }
                    {/* <Upload
                        
                        showUploadList={false}
                        onChange={(_, currentFile) => {
                            console.log(URL.createObjectURL(currentFile.originFile))
                            imgs.push(URL.createObjectURL(currentFile.originFile))
                            setImgs([...imgs])
                        }}
                    ></Upload> */}
                </div>
            </div>
            <div className='setting-item' id='setting'>
                {/* <PageHeader title=''/> */}
                <Form>
                    <Form.Item label={''}>
                        <Switch checked={JSON.parse(btnSetting)} onChange={(val)=>{
                            console.log(val)
                            setbtnSetting(val)
                            // true 
                            // false 
                            localStorage.setItem('btnSetting', val)
                        }}
                        />
                        <span style={{paddingLeft:'10px'}}>
                        {
                            JSON.parse(btnSetting) ?'':''
                        }
                        </span>
                    </Form.Item>
                    <Form.Item label="Subata">
                        <Switch checked={JSON.parse(btnSetting1)} onChange={(val)=>{
                            // console.log(val)
                            setbtnSetting1(val)
                            setSubataShow(val)
                            // true 
                            // false 
                            localStorage.setItem('btnSetting1', val)
                        }}
                        />
                        <span style={{paddingLeft:'10px'}}>
                        {
                            JSON.parse(btnSetting1) ?'':''
                        }
                        </span>
                    </Form.Item>
                    <Form.Item label="">
                        <Switch checked={JSON.parse(btnSetting2)} onChange={(val)=>{
                            // console.log(val)
                            setbtnSetting2(val)
                            // true 
                            // false 
                            localStorage.setItem('btnSetting2', val)
                        }}
                        />
                        <span style={{paddingLeft:'10px'}}>
                        {
                            JSON.parse(btnSetting2) ?'':''
                        }
                        </span>
                    </Form.Item>
                </Form>
            </div>
            <div className='setting-item' id='gameFile'>
                {/* <PageHeader title=''/> */}
                <Button type='primary' size='large' onClick={()=>{
                    window.tools.openFile(path)
                    window.electronAPI.mini()
                }}></Button>
                <br/><br/>
                <span></span>
                <span>{path?path:<Button onClick={()=>{
                        console.log(localStorage.getItem('wizInstall'))
                        let fileSelect = document.getElementById('selectWiz')
                        fileSelect.click()
                    }} status='success' type='primary'>{'Wizard.exe'}</Button>}</span>
                {/* <br/><br/>
                <Button type='primary' size='large' onClick={()=>{
                    window.tools.checkFiles(path)
                }}></Button> */}
            </div>
            {/* <div className='setting-item' id='live2d-set'>
                <Form>
                    <Form.Item label={''}>
                        <Switch checked={live2dOpen} onChange={(val)=>{
                            console.log(val)
                            setlive2dOpen(val)
                            if(val){
                                window.electronAPI.openLive2d({
                                    modelName: localStorage.getItem('live2d') || 'shizuku'
                                })
                            }else{
                                window.electronAPI.closeLive2d()
                            }
                            window.electronAPI.closedLive2d(()=>{
                                setlive2dOpen(false)
                            })
                        }}
                        />
                        <span style={{paddingLeft:'10px'}}>
                        {
                            live2dOpen ?'':''
                        }
                        </span>
                    </Form.Item>
                    <Form.Item label={''}>
                        <Radio.Group direction='vertical' defaultValue={liveName} onChange={(val)=>{
                        // console.log(val)
                        localStorage.setItem('live2d', val)
                        setLive2d(val)
                        if(live2dOpen){
                            window.electronAPI.closeLive2d()
                            window.electronAPI.openLive2d({
                                modelName: localStorage.getItem('live2d') || 'shizuku'
                            })
                            setTimeout(() => {
                                setlive2dOpen(true)
                            }, 500);
                        }
                        // alertTextLive2d('~')
                    }}>
                    {
                        models.map((item, index)=>{
                            return <Radio value={item.name} key={item.name}>
                                {item.label}
                            </Radio>
                        })
                    }
                    </Radio.Group>
                    </Form.Item>
                </Form>
            </div> */}
            <div className='setting-item' id='language'>
                <Form>
                    <Form.Item label={''}>
                        <Button
                            type='primary'
                            status='success'
                            loading={loadFile}
                            onClick={()=>{
                                setLoadFile(true)
                                console.log(window.fileList_update.split('\n'))
                                window.tools.getGameVersion((versionArr)=>{
                                    
                                    if(versionArr === undefined){
                                        Message.error('')
                                        return
                                    }
                                    // window.tools.getFile()
                                    let serverUrl
                                    versionArr.forEach((line)=>{
                                        if(line.includes('UrlPrefix')){
                                            console.log(line.split('=')[1])
                                            serverUrl = line.split('=')[1]
                                        }
                                    })
                                    let fileStatus = [], indexDownload = 0
                                    window.fileList_update.split('\n').forEach(path=>{
                                        // -1 
                                        fileStatus.push({
                                            targetUrl: serverUrl.trim() + path,
                                            outPut: window.wizPath + path,
                                            status: -1
                                        })
                                    })
                                    setLength(fileStatus.length)
                                    // console.log(fileStatus)
                                    clearInterval(downLoadTimer)
                                    downLoadTimer = setInterval(()=>{
                                        // console.log(indexDownload, fileStatus.length)
                                        if(indexDownload === (fileStatus.length - 1)){
                                            // console.log(fileStatus.find(item=>item.status !== 1))
                                            clearInterval(downLoadTimer)
                                        }
                                        // console.log(fileStatus)
                                        if(fileStatus[indexDownload].status === -1){
                                            let {targetUrl, outPut} = fileStatus[indexDownload]
                                            fileStatus[indexDownload].status = 0
                                            window.tools.getFile(targetUrl, outPut, (sign)=>{}, (total, currentTotal)=>{
                                                if(total){
                                                    setTotal(total)
                                                    setCurrent(currentTotal)
                                                    // console.log(total, currentTotal)
                                                    if(total * 1 === currentTotal * 1){
                                                        fileStatus[indexDownload].status = 1
                                                        if(!fileStatus.find(item=>item.status !== 1)){
                                                            setLoadFile(false)
                                                            setLength(0)
                                                            setCurrentFile(0)
                                                        }
                                                    }
                                                }
                                                if(!total){
                                                    fileStatus[indexDownload].status = 1
                                                    if(!fileStatus.find(item=>item.status !== 1)){
                                                        setLoadFile(false)
                                                        setLength(0)
                                                        setCurrentFile(0)
                                                    }
                                                }
                                            })
                                        }
                                        if(fileStatus[indexDownload].status === 1){
                                            indexDownload++
                                            setCurrentFile(indexDownload + 1)
                                        }
                                    }, 500)

                                    // window.tools.getFile()
                                })
                            }}
                        > {fileLength > 0 ? `(  )`:' (  )'}  </Button>
                        <br/>
                        {fileLength > 0 && `${currentFile} \n${fileLength}`}<br/>
                        {fileLength > 0 && `${((current/total)*100).toFixed(2)}%`}<br/>
                        {fileLength > 0 && ``}
                    </Form.Item>
                    <Form.Item label={''}>
                        <Switch checked={zhSound} onChange={(val)=>{
                            console.log(val)
                            // true 
                            // false 
                            localStorage.setItem('zhSound', val)
                            setZhSound(val)
                            if(val){
                                // alertTextLive2d('~')
                            }
                        }}
                        />
                        <span style={{paddingLeft:'10px'}}>
                        {
                            zhSound ?'':''
                        }()
                        </span>
                    </Form.Item>
                </Form>
            </div>
            <div className='setting-item' id='clear'>
                {/* <PageHeader title=''/> */}
                <Button status='danger' type='primary' size='large' onClick={()=>{
                    window.tools.init(()=>{
                        localStorage.clear()
                        // 
                        // alertTextLive2d('...')
                        // setTimeout(() => {
                            window.electronAPI.restart()
                        // }, 2000)
                    })
                }}></Button>
            </div>

        </div>
    </div>
}

export default Setting