import { useEffect, useState} from 'react';
import '../css/main.css'
import { List, Button, Modal, Notification, Drawer, Collapse, Message, Input, Tooltip, Form, Grid  } from '@arco-design/web-react';
import logo from '../image/WizardLogoRA.png'
import { IconClose, IconMinus, IconTool } from '@arco-design/web-react/icon';
import zfb from '../image/zfb.jpg'
import wechat from '../image/wechat.jpg'
import QQ from '../image/QQ_share.jpg'
import su from '../image/Subata_logo.png'
import apiPath from './http/api'
import RightNav from './components/right-nav';
import BodyMain from './components/body-main';
import Setting from './components/setting';
// import { alertText } from './util/dialog';
// let {alertTextLive2d} = window.electronAPI
let { Row, Col } = Grid
//'ws://localhost:8000'
let wsPath = 'ws://101.43.216.253:8000'
let box = document.getElementById('live2d-widget')
let position = []
let update = false
let timerr
let style = {
    right:'50px',
    top:'20px'
}
Notification.config({
    maxCount:'5',
    duration:5000
})
let obj = {
    r: '<>',
    d: '<>',
    c: '<>',
}
let ws = null, socketError = false
let imgMap = {
    qq:QQ,
    wx:wechat,
    zf:zfb
}
let isDown = false;  // 
let isDown_live2d = false
let baseX = 0,baseY = 0; //
let prveX = 0, prveY = 0 // XY
let useTime = 0, useTimer = null
function Main(){
    let [loading, setLoading] = useState(true) // 
    let [loading1, setLoading1] = useState(true) // List
    let [imgs, setImgs] = useState([]) // 
    let [show, setShow] = useState(false) // ModalShow
    let [zfType, setZf] = useState('') // zf
    let [img, setImg] = useState('')  // zf
    let [percent, setPercent] = useState(0) // 
    let [drawer, setDrawer] = useState(false) // 
    let [count, setCount] = useState(0)  // 
    // let [news, setNews] = useState([]) // 
    // let [activity, setActivity] = useState([])  // 
    // let [msgHeight, setHeight] = useState('95%') // 
    let [btnLoading, setBtnLoad] = useState(false) // 
    let [current, setCurrent] = useState(0)  // 
    let [total, setTotal] = useState(0) // 
    let [msgShow, setMsgShow] = useState(false) // 
    let [text, setText] = useState('')  // 
    let [title, setTitle] = useState('')  // 
    let [user, setUser] = useState(localStorage.getItem('username')) // 
    let [msgShow1, setMsgShow1] = useState(false)  // 
    let [text1, setText1] = useState('')  //  
    let [title1, setTitle1] = useState('')// 
    let [user1, setUser1] = useState('')// 
    let [message, setMessage] = useState([]) // 
    let [type, setType] = useState(localStorage.getItem('type')) // type
    let [root, setRoot] = useState(localStorage.getItem('root')||'') // 
    let [play, setPlay] = useState(localStorage.getItem('wizInstall')) // 
    let [version, setVersion] = useState('') // 
    let [nav, setNavs] = useState({})
    let [settingShow, setSetShow] = useState(false)
    let [subataShow, setSubataShow] = useState(JSON.parse(localStorage.getItem('btnSetting1')) || true)
    let [imgNum, setimgNum] = useState(localStorage.getItem('imgNum')? localStorage.getItem('imgNum')*1:0)
    useEffect(() => {
        // 
        getSteam(()=>{
            // 
            if(localStorage.getItem('type')){
                checkUpdate(false) 
            }
        })
        
        // 
        getCarousel()
        // 
        drag()
        // 
        dark()
        // 
        getData()
        clearInterval(timerr)
        timerr = setInterval(()=>{
            getData()
        },60000)
        // WebSocket
        createSocket()
        // 
        resize()
        if(localStorage.getItem('wizInstall') === 'false'){
            Notification.error({
                style,
                id:'notInstallWizard101',
                title:'Wizard101, Steam',
                content: <span>
                    <Button onClick={()=>{
                        let fileSelect = document.getElementById('selectWiz')
                        fileSelect.click()
                    }}></Button>
                </span>
            })
        }
        // 
        setInterval(()=>{
            // console.log('')
            window.electronAPI.getUpdater((data)=>{
                // console.log('message---->',data)
                if(data.cmd==='downloadProgress'){
                    update = true
                    setPercent(parseInt(data.progressObj.percent))
                    setTotal(data.progressObj.total)
                    setCurrent(data.progressObj.transferred)
                    Notification.warning({
                        title:'',
                        style,
                        id:'subata-up',
                        content:''
                    })
                    // alertTextLive2d('')
                }
            })
        }, 1000)
        // version
        window.electronAPI.getVersion((version)=>{
            setVersion(version)
        })

        window.electronAPI.ready()
        window.electronAPI.menuChangeType((type)=>{
            localStorage.setItem('type', type)
            checkUpdate()
        })
        if(JSON.parse(localStorage.getItem('accounts')) === null){
            localStorage.setItem('accounts',JSON.stringify([]))
        }
        if(JSON.parse(localStorage.getItem('accountsMap')) === null){
            localStorage.setItem('accountsMap',JSON.stringify({}))
        }
        return () => {
            // 
            destroy()
            localStorage.setItem('msgLength', message.length)
        };
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])
    useEffect(()=>{
        setImg(imgMap[zfType])
    },[zfType])
    useEffect(()=>{
        if(img)
            setShow(true)
    },[img])
    useEffect(()=>{

    },[settingShow])
    useEffect(()=>{
        setCount(0)
    },[drawer])
    useEffect(()=>{
        if(percent === 100){
            setBtnLoad(false)
            setPercent(0)
            window.electronAPI.sound()
            if(!update){
                Notification.success({
                    id:'download',
                    style,
                    title:'!',
                    content:'!',
                    duration: 2000
                })
                // alertTextLive2d(`!${useTime}`)
                setTimeout(() => {
                    useTime = 0
                }, 1000);   
            }
            // window.tools.changeType(localStorage.getItem('type'))
        }
        
        if(percent <= 3 && percent >= 1){
            // alertTextLive2d('')
            clearInterval(useTimer)
            useTimer = setInterval(() => {
                useTime+=1
            }, 1000);
        }
        if(percent >= 25 && percent <= 30){
            // alertTextLive2d('')
        }
        if(percent >= 50 && percent <= 55){
            // alertTextLive2d('')
        }
        if(percent >= 90 && percent <= 95){
            // alertTextLive2d('~')
        }
        window.electronAPI.setProgressBar(percent/100)
    },[percent])
    function getSteam(callback){
        // console.log('getSteam')
        window.tools.getPath((stdout, stderr)=>{
          console.log(stdout.split('InstallPath')[1].split('REG_SZ')[1].trim())
          if(localStorage.getItem('gameDataPath') === null){
              localStorage.setItem('gameDataPath',`${stdout.split('InstallPath')[1].split('REG_SZ')[1].trim()}\\steamapps\\common\\Wizard101\\Data\\GameData\\`)
          }
          if(localStorage.getItem('wizPath') === null){  
              localStorage.setItem('wizPath', `${stdout.split('InstallPath')[1].split('REG_SZ')[1].trim()}\\steamapps\\common\\Wizard101`)
          } 
          if(localStorage.getItem('steamPath') === null){
              localStorage.setItem('steamPath', stdout.split('InstallPath')[1].split('REG_SZ')[1].trim())
          }
          window.wizPath = localStorage.getItem('wizPath')
          window.path = localStorage.getItem('gameDataPath')
          console.log(window.wizPath)
          console.log(window.path)
          console.log('============')
          window.tools.checkGameInstall((steam, wizard, err)=>{
            // console.log(steam, wizard, err)
            if(steam){
              // Message.warning('Steam')
              localStorage.setItem('steamInstall', true)
            }else{
                localStorage.setItem('steamInstall', false)
            }
            if(wizard){
                localStorage.setItem('wizInstall', true)
                setPlay('true')
            }else{
                localStorage.setItem('wizInstall', false)
            }
          }) 
          callback()  
      }, (error)=>{
          console.log('Steam')
        //   console.log(error)
          window.tools.checkGameInstall((steam, wizard, err)=>{
            console.log('Steam:' + steam)
            console.log('Wizard:' + wizard)
            console.log('Error:' + err)
            if(steam){
              // Message.warning('Steam')
              localStorage.setItem('steamInstall', true)
            }else{
                localStorage.setItem('steamInstall', false)
            }
            if(wizard){
                localStorage.setItem('wizInstall', true)
                setPlay('true')
            }else{
                localStorage.setItem('wizInstall', false)
            }
          }) 
          callback()  
      }) 
      }
    function createSocket(){
        ws = new WebSocket(wsPath)
        ws.onopen =()=>{
            console.log('')   
            getMessage()   
        }
        ws.onerror = ()=>{
            reconnet()
        }
        ws.onclose = ()=>{
            reconnet()
        }
        
        // console.log(ws)
        ws.onmessage = (msg)=>{
            // console.log(msg.data)
            let data = JSON.parse(msg.data)
            if(data.id === localStorage.getItem('userid')){
                Message.success({
                    style:{top:'20px'},
                    content:'',
                    duration:3000,
                    onClose:()=>{
                        setTitle('')
                        setText('')
                    }
                })
            }else if(data.type === 'del'){
                getMessage()
            }else{
                window.electronAPI.sound()
                Notification.info({
                    style,
                    content:data.title,
                    title:`${data.username || ''}`
                })
                setCount(1)
            }
            getMessage()
        }
        let reconnet = ()=>{ //websock
            if(socketError)
                return false
            socketError = true
            setTimeout(()=>{
                ws = new WebSocket(wsPath)
                socketError = false
            },2000)
        }
    }
    function resize(){
        window.onresize = ()=>{
            // setHeight(window.screen.height - 40 + 'px')
        }
    }
    function getData(){
        // apiPath.mainPage().then(res=>{
        //     if(res.status === 200){
        //         setActivity([...res.data.activity])
        //         setNews([...res.data.news])
        //         setLoading1(false)
        //     }
        // })
        apiPath.getNav().then(res=>{
            // console.log(res)
            if(res.status === 200){
                // console.log(res.data.messages)
                setNavs(res.data.messages)
                setLoading1(false)
            }
        })
    }
    function dark(){
        document.body.setAttribute('arco-theme', 'dark');
    }
    function destroy(){
        document.onmousedown = null
        document.onmousemove = null
        window.onresize = null
    }
    function drag(){
        // box.addEventListener('mousedown', function(e){
        //     isDown_live2d = true // 
        //     position = [e.clientX, e.clientY]
        // })
        document.addEventListener('mousemove',function(ev){
            if(isDown){
              const x = ev.screenX - baseX
              const y = ev.screenY - baseY
            //   console.log(x, y)
              if(prveX !== x || prveY !== y){
                // console.log(x, y)
                prveX = x
                prveY = y
                window.electronAPI.sendXY({
                    x, y
                })
              }
            }

            // if(isDown_live2d){
            //     const x = ev.clientX
            //     const y = ev.clientY
            //     const deltaX = x - position[0]
            //     // const deltaY = y - position[1]
            //     const left = parseInt(box.style.left || 0)
            //     // const top = parseInt(box.style.top || 0)
            //     if(left + deltaX > 1000) return
            //     if(left + deltaX < 50) return
            //     box.style.left = left + deltaX + 'px'
            //     // box.style.top = top + deltaY + 'px'
            //     position = [x, y]
            // }
        })
        document.addEventListener('mouseup',()=>{
            isDown = false
            isDown_live2d = false
        })
    }
    function getCarousel(){
        apiPath.getCurl().then(res=>{
            // console.log(res.data.lunbo)
            setImgs([...res.data.lunbo])
            setLoading(false)
        })
    }
    function getMessage(){
        apiPath.getMessage().then(res=>{
            // console.log(res.data.message)
            setMessage([...res.data.messages.reverse()])
            if(localStorage.getItem('msgLength') && res.data.messages.length !== localStorage.getItem('msgLength')){
                setCount(1)
            }
            localStorage.setItem('msgLength', res.data.messages.length)
        })
    }
    function install(type){
        Notification.error({
            id:'notInstall_bd',
            style,
            title:'',
            btn: (
                <span style={{display:'flex', flexDirection:'column'}}>
                  {
                    type ? type==='d' && <Button
                        loading = {btnLoading}
                        type='primary'
                        size='small'
                        style={{ margin: '5px' }}
                        status='warning'
                        onClick={()=>{
                            downLoad('d')
                            setType('d')
                            localStorage.setItem('type', 'd')
                        }}
                    >
                        
                    </Button>: <Button
                        loading = {btnLoading}
                        type='primary'
                        size='small'
                        style={{ margin: '5px' }}
                        status='warning'
                        onClick={()=>{
                            downLoad('d')
                            setType('d')
                            localStorage.setItem('type', 'd')
                        }}
                    >
                        
                    </Button>
                  }
                  {
                    type?type === 'r'&&<Button 
                        loading={btnLoading}
                        style={{ margin: '5px' }}
                        status='success'
                        onClick={()=>{
                            downLoad('r')
                            setType('r')
                            localStorage.setItem('type', 'r')
                        }}
                        type='primary' 
                        size='small'
                    >
                        
                    </Button>:<Button 
                        loading={btnLoading}
                        style={{ margin: '5px' }}
                        status='success'
                        onClick={()=>{
                            downLoad('r')
                            setType('r')
                            localStorage.setItem('type', 'r')
                        }}
                        type='primary' 
                        size='small'
                    >
                        
                    </Button>
                  }
                  {
                    type?type==='c'&&<Button 
                        loading={btnLoading}
                        style={{ margin: '5px' }}
                        onClick={()=>{
                            downLoad('c')
                            setType('c')
                            localStorage.setItem('type', 'c')
                        }}
                        type='primary' 
                        size='small'
                    >
                        
                    </Button>:<Button 
                        loading={btnLoading}
                        style={{ margin: '5px' }}
                        onClick={()=>{
                            downLoad('c')
                            setType('c')
                            localStorage.setItem('type', 'c')
                        }}
                        type='primary' 
                        size='small'
                    >
                        
                    </Button>
                  }
                </span>
            ),
        })
    }
    function checkUpdate(show = true){
        console.log(obj[localStorage.getItem('type')])
        Notification.remove('change_bd') 
        // Notification.clear()
        // console.log(window.tools)
        window.tools.checkUpdate(localStorage.getItem('type'), (num)=>{
            console.log('num----->',num)
            switch (num) {
                case 1:
                    // 
                    upDate()
                    break;
                case 2:
                    // 
                    if(show)
                    window.tools.changeType(localStorage.getItem('type'),(sign)=>{
                        console.log(sign)
                        if(!sign){
                            if(show)
                                install(localStorage.getItem('type'))
                            return
                        }
                        window.electronAPI.sound()
                        Message.success({
                            style:{top:'20px'},
                            content:`${obj[localStorage.getItem('type')]}!`
                        })
                        window.electronAPI.changeType(localStorage.getItem('type'))
                        setType(localStorage.getItem('type'))
                    })
                    break
                case 3:
                    // 
                    console.log('')
                    if(show)
                        install(localStorage.getItem('type'))
                    window.electronAPI.winShow()
                    break
                default:
                    break;
            }
        },(err)=>{
            console.log(err)
        },(err)=>{
            console.log(err)
            Notification.error({
                style,
                id:'notInstallWizard101',
                title:'Wizard101, Steam',
                content: <span>
                    <Button onClick={()=>{
                        let fileSelect = document.getElementById('selectWiz')
                        fileSelect.click()
                    }}></Button>
                </span>
            })
            window.electronAPI.winShow()
        })
    }
    function upDate(){
        console.log(obj[localStorage.getItem('type')])
        Notification.warning({
            title:`${obj[localStorage.getItem('type')]}`,
            id:'update',
            style,
            duration:1000000000,
            // closable:false,
            btn: (
                <span>
                  {
                    localStorage.getItem('type') === 'd' && <Button
                        loading={btnLoading}
                        type='primary'
                        size='small'
                        style={{ margin: '0 12px' }}
                        onClick={()=>{
                            downLoad()
                        }} 
                    >
                        
                    </Button>
                  }
                  {
                    localStorage.getItem('type') === 'r' && <Button 
                        loading={btnLoading}
                        onClick={()=>{
                            downLoad()
                        }} 
                        type='primary' 
                        size='small'
                    >
                        
                    </Button>
                  }
                  {
                    localStorage.getItem('type') === 'c' && <Button 
                        loading={btnLoading}
                        onClick={()=>{
                            downLoad()
                        }} 
                        type='primary' 
                        size='small'
                    >
                        
                    </Button>
                  }
                </span>
            ),
        })
    }
    function downLoad(type){
        setBtnLoad(true)
        Notification.clear()
        window.tools.downLoad(type || localStorage.getItem('type') ,(mark)=>{
            Notification.warning({
                id:'download',
                title:'...',
                content:mark,style,duration:10000000
            })
        },(total, currentTotal)=>{
            setCurrent(currentTotal)
            setTotal(total)
            setPercent(Number.parseInt((( currentTotal / total ).toFixed(2) * 100)))
        }, (err)=>{
            if(err){
                Notification.error({
                    content:err,
                    style,
                    title:'<>'
                })
            }
        },(num)=>{
            console.log(num)
            if(num === 1){
                setBtnLoad(false)
                setPercent(0)
                window.electronAPI.sound()
                Notification.success({
                    id:'download',
                    style,
                    title:'!',
                    content:'!',
                    duration: 2000
                })
            }
            if(num === 2){
                window.electronAPI.sound()
                Notification.remove('download')
                Message.success({
                    style:{top:'20px'},
                    content:`${obj[localStorage.getItem('type')]}!`
                })
            }
        })
    }

    function changeBd(){
        Notification.info({
            title:'/',
            // closable:false,
            showIcon:false,
            duration:100000,
            id:'change_bd',
            style,
            btn: (
                <span style={{display:'flex'}}>
                  <Tooltip style={{zIndex:'999999'}} content={`NPCWiki()`}>
                    <Button
                        // loading={btnLoading}  
                        type='primary'
                        size='small'
                        status= 'warning'
                        style={{ margin: '0 12px' }}
                        disabled={type === 'd'}
                        onClick={()=>{
                            Notification.warning({
                                title:':',
                                style,
                                duration:100000,
                                id:'change_bd',
                                content:<span>
                                    <Button onClick={()=>{
                                        localStorage.setItem('type','d')
                                        setType('d')
                                        // Notification.remove('change_success')
                                        checkUpdate(localStorage.getItem('type'))
                                    }} type='primary' status='warning' size='small' style={{ margin: '0 12px 0 0' }}>
                                        
                                    </Button>
                                    <Button onClick={()=>{
                                        changeBd()
                                    }} type='primary' status='success' size='small' style={{ margin: '0 12px 0 0' }}>
                                        
                                    </Button>
                                </span>
                            })
                        }}
                    >
                        
                            
                    </Button>
                  </Tooltip>
                  <Tooltip style={{zIndex:'999999'}} content={`,npc, `}>
                    <Button onClick={()=>{
                            localStorage.setItem('type','r')
                            // Notification.remove('change_success')
                            setType('r')
                            checkUpdate(localStorage.getItem('type'))
                        }} type='primary' disabled={type === 'r'} status='success' size='small' style={{ margin: '0 12px 0 0' }}>
                        
                            
                    </Button>
                  </Tooltip>
                  <Tooltip style={{zIndex:'999999'}} content={``}>
                    <Button disabled={type === 'c'} onClick={()=>{
                            localStorage.setItem('type','c')
                            setType('c')
                            // Notification.remove('change_success')
                            checkUpdate(localStorage.getItem('type'))
                        }} type='primary' size='small'>
                        
                            
                    </Button>
                  </Tooltip>
                </span>
            ),
        })
    }
    return <div className="main">
        <div className={`bottom-bg bottom-bg${imgNum}`}>
            <img alt='' src=''/>
        </div>
        <div className='nav' 
            onMouseDown={(e)=>{
                e.stopPropagation()
                isDown = true 
                baseX = e.clientX
                baseY = e.clientY
                // console.log(baseX, baseY)
            }}
        >
            <div className='nav-logo'><img alt='' src={su}/></div>
            <div className='nav-title'>Subata{`@${version}`}<Button onMouseDown={(e)=>{e.stopPropagation()}} type='text' onClick={(e)=>{
                e.stopPropagation()
                window.electronAPI.openBroswer('https://subata.top/index.php/2022/09/16/startgame/')
            }} status='success'></Button></div>
            {/* <div className='nav-title'> {obj[type]}</div> */}
            <div className='nav-control'
                onMouseDown={(e)=>{
                    e.stopPropagation()
                    isDown = false
                    // console.log(baseX, baseY)
                }}
            >
                <div className='control-btn' onClick={(e)=>{
                    e.stopPropagation()
                    // 
                    setSetShow(true)
                }}>
                    
                    <IconTool style={{fontSize:'20px'}}/>
                </div>
                <div className='control-btn' onClick={(e)=>{
                    e.stopPropagation()
                    window.electronAPI.mini()
                }}>
                    <IconMinus style={{fontSize:'20px'}}/>
                </div>
                <div className='control-btn btn-danger' onClick={(e)=>{
                    e.stopPropagation()
                    if(JSON.parse(localStorage.getItem('btnSetting2'))){
                        window.electronAPI.winHide()
                    }else{
                        window.electronAPI.close()
                    }
                }}>
                    <IconClose style={{fontSize:'20px'}}/>
                </div>
            </div>
        </div>
        <div className='body'>

            <BodyMain
                logo={logo}
                loading={loading}
                imgs = {imgs}
                nav = {nav}
                loading1 = {loading1}
                btnLoading = {btnLoading}
                percent={percent}
                current={current}
                total = {total}
                play = {play}
                subataShow={subataShow}
            />
            <RightNav
                onMouseDown={(init)=>{
                    let { down, X, Y } = init
                    isDown = down
                    baseX = X
                    baseY = Y
                }}
                setZf = {setZf}
                changeBd={changeBd}
                install = {install}
                setDrawer = {setDrawer}
                btnLoading = {btnLoading}
                count = {count}
            />
        </div>
        <Drawer 
            visible={drawer}
            footer={(
                <span>
                    {<Button onClick={()=>{
                        setMsgShow(true)
                    }}></Button>}
                </span>
            )}
            onOk={() => {
                setDrawer(false);
            }}
            onCancel={() => {
                setDrawer(false);
            }}
            title=""
            escToExit
            width={350}
            closable={false}
            maskClosable
            style={{
                top:'40px'
            }}
            bodyStyle={{
                background:'rgb(104 104 104)',
                padding:0
            }}
        >
            <Collapse       
                style={{ width: '100%' }}
            >
                {
                        <List
                            dataSource={message}
                            noDataElement={<></>}
                            render={(item, index) => item.del? null : <List.Item key={index} onClick={()=>{
                                setMsgShow1(true)
                                setTitle1(item.title)
                                setUser1(item.username)
                                setText1(item.msg)
                            }}><span><Tooltip position='bl' content={item.time.split(' ')[0]}>{`${item.title}-${item.username||'Subata'}`}</Tooltip> {localStorage.getItem('userid')===item.id && <Button type='text' onClick={(e)=>{
                                e.preventDefault()
                                Notification.warning({
                                    style,
                                    id:'delmessage',
                                    title:'?',
                                    content:<><Button onClick={()=>{
                                        apiPath.delMessage({id:item.msgid}).then(res=>{
                                            if(res.data.success){
                                                Message.success({
                                                    style:{top:'20px'},
                                                    content:''
                                                })
                                            }
                                            Notification.remove('delmessage')
                                            ws.send(JSON.stringify({type:'del'}))
                                        })
                                    }}></Button></>
                                })
                            }}></Button>}</span></List.Item>}
                        />
                    
                }
            </Collapse>
        </Drawer>
        <Modal
            title=''
            simple
            style={{textAlign:'center'}}
            visible={show}
            onCancel={()=>{
                setZf('')
                setShow(false)
                if(zfType !== 'qq'){
                    Message.info({
                        content:'',
                        style:{top:'20px'},
                        duration:2000
                    })
                }
            }}
            children={[<img key={1} className='zf-img' src={img} alt=''/>]}
            footer={null}
        />
        <Modal
            title=''
            style={{textAlign:'center'}}
            visible={msgShow}
            maskClosable={false}
            onCancel={()=>{
                setMsgShow(false)
            }}
            children={<>
                <Input 
                    placeholder=''
                    type='text'
                    maxLength={15}
                    value={user}
                    onChange = {(val)=>{
                        setUser(val)
                        localStorage.setItem('username',val)
                    }}
                />
                <Input 
                    placeholder=''
                    type='text'
                    value={title}
                    maxLength={20}
                    onChange = {(val)=>{
                        setTitle(val)
                    }}
                />
                <Input.TextArea
                    placeholder=''
                    style={{
                        height:'300px'
                    }}
                    value={text}
                    onChange={(val)=>{
                        setText(val)
                    }}
                />
                {
                    root !== 'wizard-subata-lsmhq' && <Input 
                        placeholder=''
                        type='text'
                        value={root}
                        onChange = {(val)=>{
                            setRoot(val)
                            if(val === 'wizard-subata-lsmhq'){
                                localStorage.setItem('root','wizard-subata-lsmhq')
                            }
                        }}
                    />
                }
            </>}
            footer={(<span>
                {
                   root === 'wizard-subata-lsmhq' && <Button type='primary' status='success' onClick={()=>{
                        
                        let data = {
                            msg:text,
                            title:title,
                            id:localStorage.getItem('userid'),
                            username:user,
                            msgid:Math.random()
                        }
                        if(text.length === 0) return
                        if(title.length === 0) return
                        if(user.length === 0) return
                        ws.send(JSON.stringify(data))
                        setMsgShow(false)
                    }}></Button>
                }
            </span>)}
        />
         <Modal
            title={title1}
            style={{textAlign:'center'}}
            visible={msgShow1}
            maskClosable={true}
            onCancel={()=>{
                setMsgShow1(false)
            }}
            children={<>
                <Input 
                    placeholder=''
                    type='text'
                    maxLength={15}
                    readOnly
                    value={user1}
                    autoFocus={false}
                />
                <Input 
                    placeholder=''
                    type='text'
                    value={title1}
                    readOnly
                    autoFocus={false}
                    // maxLength={20}
                    onChange = {(val)=>{
                        setTitle1(val)
                    }}
                />
                <Input.TextArea
                    placeholder=''
                    style={{
                        height:'300px'
                    }}
                    readOnly
                    autoFocus={false}
                    value={text1}
                    onChange={(val)=>{
                        setText1(val)
                    }}
                />
            </>}
            footer={null}
        />
        <Modal
            title={''}
            // style={{textAlign:'center'}}
            visible={settingShow}
            maskClosable={false}
            onCancel={()=>{
                setSetShow(false)
            }}
            style={{
                maxHeight:'600px',
                minHeight:'600px',
                width:'700px',
            }}
            children={<Setting
                setBg={setimgNum}
                setSubataShow = {setSubataShow}
            />}
            footer={null}
        />
        <input id='selectWiz' directory="" nwdirectory="" type='file' accept='.exe' onChange={(e)=>{
            console.log(e.target.files[0].path)
            if(e.target.files[0].path.includes('Wizard101.exe')){
                console.log('---')
                localStorage.setItem('wizPath', e.target.files[0].path.split('Wizard101.exe')[0])
                localStorage.setItem('gameDataPath', e.target.files[0].path.split('Wizard101.exe')[0]+'Data\\GameData\\')
                window.wizPath = e.target.files[0].path.split('Wizard101.exe')[0]
                window.path = e.target.files[0].path.split('Wizard101.exe')[0]+'Data\\GameData\\'
                getSteam(()=>{
                    if(localStorage.getItem('type')){
                        checkUpdate(false) 
                    }
                    localStorage.setItem('wizInstall', 'true')
                    setPlay('true')
                })
            }else{
                Message.error({
                    content:' PS:Wizard101.exe',
                    style:{
                        top:'20px'
                    }
                })
            }
            e.target.value = ''
            // e.target.files = []
        }} style={{opacity:0, position:'absolute',width:0, height:0, top:'1000px'}}/>
    </div>    
}

export default Main